	module.exports = {"data":{"markdownRemark":{"html":"<h2>Flexbox</h2>\n<p>Flexbox is a new way to structure content in CSS3. It provides a wonderful way to create responsive websites that work well across different screen sizes and order content.</p>\n<p>There are 3 simple steps to use flexbox.</p>\n<ol>\n<li>Convert parent container to a flex container by using <i>display:flex;</i> in the css section</li>\n<li>Adjust arrangement of different containers using <i>flex-direction</i></li>\n<li>Adjust individual items by using properties like justify-content, align-items etc.</li>\n</ol>\n<p>Read on for more information for these properties :)</p>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p><a href='https://medium.freecodecamp.org/an-animated-guide-to-flexbox-d280cf6afc35' target='_blank' rel='nofollow'>This is a great article</a> to read up to understand more about flexbox</p>","fields":{"slug":"/css/layout/flexbox/"},"frontmatter":{"title":"Flexbox"}}},"pathContext":{"slug":"/css/layout/flexbox/"}}

